import { mapState } from 'vuex';
import appOrderGoodsInfo from "../../../components/page-component/app-order-goods-info/app-order-goods-info.vue";
import appFormData from "../../../components/basic-component/app-order/app-form-data.vue";

export default {
    components: {
        'app-order-goods-info': appOrderGoodsInfo,
        appFormData,
    },
    data() {
        return {
            haveForm: true,
            id: null,
            orderDetail: {},
            clerk_remark: '',
            is_show: false,
            msg: false,
            remarkClerk: false,
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            clerkImg: state => state.mallConfig.__wxapp_img.clerk,
        })
    },
    methods: {
        showForm(e) {
            this.haveForm = e && e.length ? true : false
        },
        toIndex() {

            uni.redirectTo({
                url: '/pages/index/index'
            });
        },
        getOrderDetail() {
            this.$request({
                url: this.$api.order.detail,
                data: {
                    id: this.id,
                    action_type: 1
                }
            }).then(response => {
                this.$hideLoading();
                this.is_show = true;
                if (response.code === 0) {
                    this.orderDetail = response.data.detail;
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
        IsClerk() {
            this.$showLoading();
            this.$request({
                url: this.$api.user.is_clerk,
                data: {
                    clerk_id: this.userInfo.options.user_id,
                }
            }).then(response => {
                if (response.code === 0) {
                    if(response.data.is_clerk_user == 1) {
                        this.getOrderDetail();
                    }else {
                        this.$hideLoading();
                        this.msg = true;
                    }
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
        clerkAffirmPay() {
            let self = this;
            uni.showModal({
                title: '提示',
                content: '确认已进行线下收款?',
                success: function (res) {
                    if (res.confirm) {
                        uni.showLoading({
                            title: '加载中'
                        });
                        self.$request({
                            url: self.$api.order.clerk_affirm_pay,
                            data: {
                                id: self.id,
                                action_type: 1,
                            }
                        }).then(response => {
                            uni.hideLoading();
                            if (response.code === 0) {
                                self.getOrderDetail();
                            } else  {
                                uni.showToast({
                                    title: response.msg,
                                    icon: 'none',
                                });
                            }
                            this.msg = response.data.msg
                        }).catch(() => {
                            uni.hideLoading();
                        });
                    }
                }
            });
        },
        orderClerk() {
            let self = this;

            if (self.remarkClerk) {
                self.remarkClerk = false;
            }

            uni.showModal({
                content: '是否核销订单？',
                success: function (res) {
                    if (res.confirm) {
                        self.$showLoading();
                        self.$request({
                            url: self.$api.order.order_clerk,
                            data: {
                                id: self.id,
                                action_type: 1,
                                clerk_remark: self.clerk_remark
                            }
                        }).then(response => {
                            self.$hideLoading();
                            uni.showToast({
                                title: response.msg,
                                icon: 'none',
                                duration: 2000,
                                success: function() {
                                    if (response.code === 0) {
                                       setTimeout(()=> {
                                            uni.redirectTo({
                                                url: '/plugins/clerk/order/order?status=1&type=1'
                                            })
                                       }, 2000);
                                    }
                                }
                            })
                        }).catch(() => {
                            self.$hideLoading();
                        });
                    }
                }
            })
        },
        orderClerkRemark()
        {
            this.remarkClerk = true;
        },
        closeDialog() {
            if (this.msg == '核销成功') {
                this.msg = '';
                if (this.is_clerk) {
                    let pages = getCurrentPages();
                    let idx;
                    pages.forEach((row, index) => {
                        if (pages[index].route === 'plugins/clerk/order/order') {
                            idx = index
                        }
                    });
                    if (idx > -1) {
                        pages[idx]._num = 1;
                        uni.navigateBack({
                            delta: pages.length - 1 - idx
                        });
                    } else {
                        uni.redirectTo({
                            url: '/plugins/clerk/order/order?status=1&type=1'
                        })
                    }
                } else {
                    uni.redirectTo({
                        url: '/pages/index/index'
                    })
                }
            } else {
                this.msg = '';
            }
        },
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.id = options.id;
        var getUser = setInterval(() => {
            if(that.userInfo) {
                that.IsClerk();
                clearInterval(getUser);
            }
        },500)
    }
}
